/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}


/* 페이지 콘텐츠 */
.wrapper{
    display: grid;
    grid-template-columns: 1.1fr 0.82fr 1.1fr;
    grid-template-rows: 35px 8px 1fr;
    position: absolute;
    width: 1100px;
    height: 58%;
    top: 25%;
    left: 50%;
    margin-left: -550px;
    padding: 30px 0;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    animation: fadeInWrapper 0.8s forwards;
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}

/* 콘텐츠 메뉴 */
.menu{
    margin-left: 40px;
    font-size: 22px;
    font-weight: bold;
    -webkit-animation: fadeInMenu 0.4s 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: fadeInMenu 0.4s 1s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@-webkit-keyframes fadeInMenu {
    0% {
        -webkit-transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
        opacity: 1;
    }
}
@keyframes fadeInMenu {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

/* 콘텐츠 스크롤바 */
.scroll{
    height: 3px;
    margin-left: 40px;
    background: rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInScrollbar 0.5s 1.6s forwards;
}
.scroll > span{
    content: '';
    height: 3px;
    position: absolute;
    background: black;
}
@keyframes fadeInScrollbar{ 100%{ opacity:1; } }

/* 콘텐츠 */
.content{
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    margin-left: 40px;
    overflow: auto;
}
.content::-webkit-scrollbar{ display: none; }
.content .per_skill{
    width: 100px;
    height: 100px;
    margin: 8px;
    border: 1px solid white;
    border-radius: 10px;
    opacity: 0;
    background: skyblue;
    -webkit-animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
	animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
}
.content .per_cert{
    width: 100%;
    height: 80px;
    margin: 8px;
    border: 1px solid white;
    border-radius: 10px;
    opacity: 0;
    background: skyblue;
    -webkit-animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
	animation: fadeInContent 0.5s 2s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
}
.content .per_skill:hover, .content .per_cert:hover{
    border: 1px solid black;
}
@-webkit-keyframes fadeInContent {
    0% {
        -webkit-transform: scaleY(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: scaleY(1);
        opacity: 1;
    }
}
@keyframes fadeInContent {
    0% {
        transform: scaleY(0);
        opacity: 1;
    }
    100% {
        transform: scaleY(1);
        opacity: 1;
    }
}

/* 정보 표시 디스플레이 */
.info{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column: 3;
    width: 80%;
    height: calc(90% - 6px);
    margin-top: calc(5% - 3px);
    border: 3px solid rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInInfo 1s 1.6s forwards;
}
@keyframes fadeInInfo{
    100%{ opacity:1; }
}


/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 28px 5px 1.2fr 1fr;
        width: 800px;
        height: 65%;
        top: 20%;
        margin-left: -430px;
        padding: 30px;
        grid-column-gap: 60px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:20%; }
    }
    .menu{ margin-left: 0; font-size:18px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:83px; height:83px;}
    .content .per_cert{ height: 83px;}
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
        margin-top: calc(3% - 3px);
    }
}

@media screen and (max-width:880px){
    .text_line{ font-size:50px;}
    @keyframes textSize{
        0%{ font-size:50px; }
        100%{ font-size:35px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 22px 5px 1fr 1fr;
        width: 500px;
        height: 70%;
        margin-left: -280px;
        top: 16%;
        grid-column-gap: 50px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:16%; }
    }
    .menu{ margin-left: 0; font-size: 15px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:70px; height:70px; margin:5px; }
    .content .per_cert{ height: 70px; margin:5px; }
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }
}

@media screen and (max-width:580px){
    .text_line{ font-size:40px;}
    @keyframes textSize{
        0%{ font-size:40px; }
        100%{ font-size:28px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-43%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 20px 5px 1fr 1fr;
        width: 350px;
        height: 75%;
        margin-left: -205px;
        top: 11.5%;
        grid-column-gap: 30px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:11.5%; }
    }
    .menu{ margin-left: 0; font-size: 13px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:50px; height:50px; margin:3px;}
    .content .per_cert{ height: 50px; margin:3px; }
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }
}