.Contact{
    width: 100%;
    background: transparent;
}
.wrapper{
    width: 1200px;
    height: 100vh;
    margin-left: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: white;
    transition: 0.25s;
}

/* Contact 페이지 타이틀 */
.title{
    width: 100%;
    height: 50px;
    text-align: center;
    font-size: 26px;
    font-family: 'dangdanghae';
}

/* Contact 페이지 메일 및 연결 정보 */
.form{
    width: 350px;
    height: 380px;
}
.form #info{
    display: grid;
    grid-template-columns: 120px 230px;
    margin-top: 40px;
}
.form #info div{
    width: 50px;
    height: 50px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.form #info div img{
    width: 50px;
}
.form div p{
    font-size: 18px;
    font-family: 'simplehae';
}
.form #info #link{
    text-decoration: underline;
    color: blue;
}
.form #info #link:hover{
    color: gray;
}




@media screen and (max-width: 1467px){
    .wrapper{width:1000px;}
}
@media screen and (max-width: 1267px){
    .wrapper{width:850px;}
}
@media screen and (max-width: 1117px){
    .wrapper{width:100%; margin-left:0;}
}
@media screen and (max-width: 750px){
    .title{font-size:23px;}
    .form{width:320px;}
    .form #info{grid-template-columns: 100px 220px;}
    .form #info div{width:40px; height:40px;}
    .form #info div img{width:40px;}
    .form div p{font-size:16px;}
}