/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}



.wrapper{
    width: 1000px;
    height: 70%;
    position: absolute;
    left: 50%;
    top: 25%;
    display: grid;
    margin-left: -500px;
}


/* PROFILE 페이지 나의 소개 영역 */
.me{
    display: grid;
    grid-template-columns: 300px 1fr;
    align-items: center;
    justify-items: center;
}
.me #photo{                /* 프로필 이미지 */
    width: 160px;
    height: 210px;
    border: 1px solid gray;
}
.me #intro{ width:100%; height:180px;}
.me #intro > p{
    font-size:20px;
    font-family: 'simplehae';
}
.me #intro > div{
    width: 550px;
    height: 60px;
    padding: 20px 30px;
    font-size: 22px;
    font-family: 'shiningstar';
    line-height: 28px;
    border-left: 3px solid gray;
}
.me #intro > div:before{
    content: '';
    position: absolute;
    right: 0;
    width: 613px;
    height: 100px;
    margin: -20px 84px;
    border-left: 3px solid black;
    background: white;
    animation: eraseBlanket 1s 1s forwards;
}
@keyframes eraseBlanket{
    0%{ width:613px; }
    100%{ width:0; }
}


/* PROFILE 페이지 경험 영역 */
.exp{
    width: 100%;
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
}
.exp > div{
    width: 280px;
    font-family: 'simplehae';
    color: rgb(78, 78, 78);
}
.exp > div > div{
    color: black;
    font-weight: bold;
    font-size: 18px;
}
.exp > div > div:before{
    content: '';
    height: 5px;
    position: absolute;
    bottom: 0;
    background: black;
}




/* 창 크기 변경 미디어 쿼리 */
@media screen and (max-width:1350px){
    .text_line{ font-size:70px; }
    @keyframes textSize{
        0%{ font-size:70px; }
        100%{ font-size:50px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-38%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 28px 5px 1.2fr 1fr;
        width: 800px;
        height: 65%;
        top: 20%;
        margin-left: -430px;
        padding: 30px;
        grid-column-gap: 60px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:20%; }
    }
    .menu{ margin-left: 0; font-size:18px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:83px; height:83px;}
    .content .per_cert{ height: 83px;}
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
        margin-top: calc(3% - 3px);
    }
}

@media screen and (max-width:880px){
    .text_line{ font-size:50px;}
    @keyframes textSize{
        0%{ font-size:50px; }
        100%{ font-size:35px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-40%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 22px 5px 1fr 1fr;
        width: 500px;
        height: 70%;
        margin-left: -280px;
        top: 16%;
        grid-column-gap: 50px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:16%; }
    }
    .menu{ margin-left: 0; font-size: 15px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:70px; height:70px; margin:5px; }
    .content .per_cert{ height: 70px; margin:5px; }
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }
}

@media screen and (max-width:580px){
    .text_line{ font-size:40px;}
    @keyframes textSize{
        0%{ font-size:40px; }
        100%{ font-size:28px; }
    }
    @keyframes moveUp{
        0%{ top:0%; }
        100%{ top:-43%; }
    }
    .wrapper{
        grid-template-columns: 1.1fr 0.9fr;
        grid-template-rows: 20px 5px 1fr 1fr;
        width: 350px;
        height: 75%;
        margin-left: -205px;
        top: 11.5%;
        grid-column-gap: 30px;
    }
    @keyframes fadeInWrapper{
        0%{ top:110%; }
        100%{ top:11.5%; }
    }
    .menu{ margin-left: 0; font-size: 13px; }
    .scroll{ margin-left: 0; }
    .content{ margin-left: 0; }
    .content .per_skill{ width:50px; height:50px; margin:3px;}
    .content .per_cert{ height: 50px; margin:3px; }
    .info{
        grid-row: 4;
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }
}