/* 타이틀 박스 */
.wrapper{
    width: 400px;
    height: 230px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left:-230px;
    margin-top: -145px;
    padding: 30px 30px;
    border-radius: 20px;
    backdrop-filter: blur(10px);
    box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
}


/* 포트폴리오 타이틀 */
.title{
    width: max-content;
    height: 60px;
    margin-top: 10px;
    font-size: 45px;
    display: inline-block;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    overflow: hidden;
    border-radius: 20px;
}
.title:before {
    content: "";
    position: absolute;
    bottom: 0;
    background: black;
    height: 3px;
}
.title:nth-of-type(1):before{ animation:bottomLine 1.3s forwards; }
.title:nth-of-type(2):before{ animation:bottomLine 1.3s 0.2s forwards; }
.title:nth-of-type(3):before{ animation:bottomLine 1.3s 0.4s forwards; }
.text{ margin-top: 60px; }
.title:nth-of-type(1) .text{ animation:textUp 0.5s 0.5s forwards; }
.title:nth-of-type(2) .text{ animation:textUp 0.5s 0.7s forwards; }
.title:nth-of-type(3) .text{ animation:textUp 0.5s 0.9s forwards; }
/* 타이틀 하단 밑줄 키프레임 */
@keyframes bottomLine{
    0%{ right:100%; left:0%;}
    30%{ right:0%; left:0%; }
    80%{ right:0%; left:0%; }
    100%{ right:0%; left:100%; }
}
/* 타이틀 하단에서 올라오는 키프레임 */
@keyframes textUp{
    100%{ margin-top:0; }
}


/* 메뉴 */
.menu > div{
    width: 200px;
    height: 120px;
    position: absolute;
    padding: 0 20px;
    cursor: pointer;
}
.menu > div > div{
    font-size: 60px;
    font-weight: bold;
    line-height: 100px;
    transition: 0.5s;
}
.menu > div:nth-child(1){top:5%; left:20%; text-shadow:20px 20px rgb(220, 220, 220); -webkit-animation:fadeInM1 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; animation:fadeInM1 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;}
.menu > div:nth-child(2){top:5%; right:20%; text-shadow:-20px 20px rgb(220, 220, 220); -webkit-animation:fadeInM2 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; animation:fadeInM2 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;}
.menu > div:nth-child(3){bottom:5%; left:20%; text-shadow:20px -20px rgb(220, 220, 220); -webkit-animation:fadeInM3 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; animation:fadeInM3 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;}
.menu > div:nth-child(4){bottom:5%; right:20%; text-shadow:-20px -20px rgb(220, 220, 220); -webkit-animation:fadeInM4 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; animation:fadeInM4 0.6s 1.5s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;}
@-webkit-keyframes fadeInM1 {
    0% { -webkit-transform: translateX(-50px) translateY(-50px); opacity: 0; }
    100% { -webkit-transform: translateX(0) translateY(0); opacity: 1; }
}
@keyframes fadeInM1 {
    0% { transform: translateX(-50px) translateY(-50px); opacity: 0; }
    100% { transform: translateX(0) translateY(0); opacity: 1; }
}
@-webkit-keyframes fadeInM2 {
    0% { -webkit-transform: translateX(50px) translateY(-50px); opacity: 0; }
    100% { -webkit-transform: translateX(0) translateY(0); opacity: 1; }
}
@keyframes fadeInM2 {
    0% { transform: translateX(50px) translateY(-50px); opacity: 0; }
    100% { transform: translateX(0) translateY(0); opacity: 1; }
}
@-webkit-keyframes fadeInM3 {
    0% { -webkit-transform: translateX(-50px) translateY(50px); opacity: 0; }
    100% { -webkit-transform: translateX(0) translateY(0); opacity: 1; }
}
@keyframes fadeInM3 {
    0% { transform: translateX(-50px) translateY(50px); opacity: 0; }
    100% { transform: translateX(0) translateY(0); opacity: 1; }
}
@-webkit-keyframes fadeInM4 {
    0% { -webkit-transform: translateX(50px) translateY(50px); opacity: 0; }
    100% { -webkit-transform: translateX(0) translateY(0); opacity: 1; }
}
@keyframes fadeInM4 {
    0% { transform: translateX(50px) translateY(50px); opacity: 0; }
    100% { transform: translateX(0) translateY(0); opacity: 1; }
}
.menu div:nth-child(1):hover > div{margin-left: 20px; margin-top: 20px; text-shadow: 0 0 rgb(220, 220, 220);}
.menu div:nth-child(2):hover > div{margin-left: -20px; margin-top: 20px; text-shadow: 0 0 rgb(220, 220, 220);}
.menu div:nth-child(3):hover > div{margin-left: 20px; margin-top: -20px; text-shadow: 0 0 rgb(220, 220, 220);}
.menu div:nth-child(4):hover > div{margin-left: -20px; margin-top: -20px; text-shadow: 0 0 rgb(220, 220, 220);}



/* 메뉴 화살표 */
.menu div span:before{
    content:'';
    position: absolute;
    height:2px;
    background: black;
    transition:0.7s;
}
.menu div span:nth-child(1):before{width:90px; bottom:20px; left:127px;}
.menu div span:nth-child(2):before{width:15px; bottom:25px; right:20px; transform:rotate(45deg);}
.menu div span:nth-child(3):before{width:15px; bottom:15px; right:20px;; transform:rotate(-45deg);}
.menu div:hover > span:nth-child(1):before{width:0;}
.menu div:hover > span:nth-child(2):before{width:0;}
.menu div:hover > span:nth-child(3):before{width:0;}